.aboutpage {
  align-items: flex-start;
  border: 1px none;
  display: flex;
  min-width: 340px;
}

.overlap-group {
  height: 711px;
  position: relative;
  width: 340px;
}

.about-1 {
  align-items: center;
  background-color: var(--white);
  display: flex;
  flex-direction: column;
  gap: 26px;
  height: 711px;
  justify-content: center;
  left: 0;
  padding: 173px 106px;
  position: absolute;
  top: 0;
  width: 340px;
}

.gabriel-dellaiuto-about-1 {
  letter-spacing: 0;
  line-height: 13.2px;
  margin-left: -42.00px;
  margin-right: -42.00px;
  margin-top: -13.50px;
  text-align: justify;
  transform: translate(5000px, 0);
  width: 212px;
}

.gabriel-dellaiuto-about-1.animate-enter3 {
  animation: animate-enter3-frames 1.50s ease-in-out 0.10s 1 normal forwards;
  transform: translate(5000px, 0);
}

.download-rsum-about-1 {
  color: var(--chicago);
  cursor: pointer;
  font-family: var(--font-family-suisse_intl-medium);
  font-size: var(--font-size-m);
  font-weight: 500;
  letter-spacing: 0;
  line-height: 13.2px;
  margin-left: -42.00px;
  margin-right: -42.00px;
  text-align: center;
  text-decoration: underline;
  transform: translate(2000px, 0);
  transition: all 0.3s ease-out;
  white-space: nowrap;
  width: 212px;
}

.download-rsum-about-1.animate-enter4 {
  animation: animate-enter4-frames 1.00s ease-in-out 0.30s 1 normal forwards;
  transform: translate(2000px, 0);
}

.download-rsum-about-1:hover {
  transform: scale(1.1);
}

.gz-or-grayz-gz-or-about-1 {
  letter-spacing: 0;
  line-height: 13.2px;
  margin-bottom: -11.50px;
  margin-left: -42.00px;
  margin-right: -42.00px;
  text-align: justify;
  transform: translate(2000px, 0);
  width: 212px;
}

.span3 {
  text-decoration: underline;
}

.gz-or-grayz-gz-or-about-1.animate-enter5 {
  animation: animate-enter5-frames 1.00s ease-in-out 0.20s 1 normal forwards;
  transform: translate(2000px, 0);
}

@keyframes animate-enter5-frames {
  from{opacity: 1;
transform: translate(2000px, 0);
}
to{opacity: 1;
transform: translate(0,0);
}
}

@keyframes animate-enter4-frames {
  from{opacity: 1;
transform: translate(2000px, 0);
}
to{opacity: 1;
transform: translate(0,0);
}
}

@keyframes animate-enter3-frames {
  from{opacity: 1;
transform: translate(5000px, 0);
}
to{opacity: 1;
transform: translate(0,0);
}
}

