.start-menu-frame-1 {
  align-items: center;
  align-self: center;
  flex-direction: column;
  gap: 8px;
  position: relative;
  transform: translate(2000px, 0);
  width: fit-content;
}

.start-menu-frame-1.animate-enter2 {
  animation: animate-enter2-frames 1.00s ease-in-out 0.20s 1 normal forwards;
  transform: translate(2000px, 0);
}

@keyframes animate-enter2-frames {
  from{opacity: 1;
transform: translate(2000px, 0);
}
to{opacity: 1;
transform: translate(0,0);
}
}

