.comp-1-3-loading {
  background-position: 50% 50%;
  background-size: cover;
  filter: blur(10px);
  height: 105px;
  min-width: 103px;
  opacity: 0.2;
}

.comp-1-3-loading.animate-enter10 {
  animation: animate-enter10-frames 2.00s ease-in-out 0.00s 1 normal forwards;
  filter: blur(10px);
  opacity: 0.2;
}

@keyframes animate-enter10-frames {
  from{opacity: 0.2;
filter: blur(10px);
}
to{opacity: 1;
filter: none;
}
}

